/* Slick Slider */
.slick-slider {
    display: block;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
    touch-action: auto;
    -ms-touch-action: auto;
}
.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }

    .slick-loading & {
        visibility: hidden;
    }
}
.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    outline: none;
    [dir="rtl"] & {
        float: right;
    }
    img {
        display: block;
    }
    &.slick-loading img {
        display: none;
    }

    display: none;

    &.dragging img {
        pointer-events: none;
    }

    .slick-initialized & {
        display: block;
    }

    .slick-loading & {
        visibility: hidden;
    }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }
}
.slick-arrow.slick-hidden {
    display: none;
}

.slick-dots {
    padding-left: 0;
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    width: auto;
    list-style-type: none;

    li {
        display: inline-block;
        margin-right: 5px;

        button {
            text-indent: -900em;
            overflow: hidden;
            width: 10px;
            height: 10px;
            background: #fff;
            border-radius: 50%;
            padding: 0;
            opacity: 0.6;
            transition: 0.2s ease-in-out;
        }

        &.slick-active,
        &:hover  {
            button {
                opacity: 1;
            }
        }

    }
}

.slick-prev,
.slick-next {
    position: absolute;
    z-index: 1;
    top: 50%;
    transform: translateY(-50%);
    color: #fff;
    width: 40px;
    height: 40px;
    text-indent: -900em;
    overflow: hidden;
    background: transparent;
    transform: rotate(45deg);
    padding: 0;
    transition: 0.2s ease-in-out;
    opacity: 0.5;
    outline: 0;

    &:hover {
        opacity: 1;
    }

}

.slick-next {
    border-top: 5px solid #fff;
    border-right: 5px solid #fff;
    right: 50px;
}

.slick-prev {
    border-bottom: 5px solid #fff;
    border-left: 5px solid #fff;
    left: 50px;
}