/*** WYSIWYG ***/

.blackhat-content{
	position: relative;
	h2, h1{
		text-transform: uppercase;
		margin-bottom: 30px;
	}
	p{
		display: block;
		margin-bottom: 30px;
	}
	/*p:last-of-type{
		margin-bottom: 0px;
	}*/
	ul{
		list-style: none;
		list-style-position: inside;
		margin-bottom: 30px;
		li{
			position: relative;
			padding-left: 15px;
			font-size: 18px;
			line-height: 24px;
			&:before{
				position: absolute;
				left: 0;
				top: 0;
				z-index: 99;
				display: block;
				vertical-align: top;
				content: '.';
				font-size: 30px;
				line-height: 5px;
			}
		}
		&.gform_fields{
			li{
				&:before{
					content: '';
				}
			}
		}
	}
	img {
		max-width: 100%;
	}
	.wp-caption{
		width: 100% !important;
		max-width: 100%;
		img {
			width: 100% !important;
			height: auto !important;
		}
		.wp-caption-text{
			
		}
	}
}