/*** Files ***/

.blackhat-files{
	
    &__file{
        
        margin-bottom: 20px;
		padding: 30px;
		border-left: 4px solid transparent;
		border-radius: 5px;
		box-shadow: rgba(0,0,0,.2) 0px 1px 5px;
		transition: all 0.1s ease-in-out;
        
        &:hover{
			border-left: 4px solid $color_brand;
			.file__link{
				&:before{
					width: 20px;
				}
			}
		}
        
		.file__name{
            margin-bottom: 10px;
			font-size: 24px;
			color: $color_brand; 
        }
        
        .file__description{
            width: 100%;
            
            p{
                font-size: 16px;
                line-height: 24px;
                margin-bottom: 10px;
            }
        }
        
        .file__link{
            font-size: 14px;
			font-weight: 300;
			transition: all 0.1s ease-in-out;
			&:before{
				width: 0;
				overflow: hidden;
				display: inline-block;
				vertical-align: middle;
				font-size: 14px;
				line-height: 14px;
				font-family: "FontAwesome";
				content: "\f019";
				transition: all 0.1s ease-in-out;
			}
            
			span{
				color: #b7b7b7;
			}
        }
	}
}