/*** Accordion ***/

.blackhat-accordion__panel {
	clear: both;

}

.panel-title {
	padding: 10px;
	font-size: 14px;
	cursor: pointer;
	margin: 0;
	color: $color_button_text;
	background: $color_button_background;
	border-bottom: 1px solid $color_button_background_hover;
	transition: all 0.1s ease-in-out;

	&:hover{
		color: $color_button_text_hover;
		background: $color_button_background_hover;
	}

}
.current .panel-title {
	color: $color_button_text_hover;
	background: $color_button_background_hover;
}

.panel-content {
	display: none;
	padding: 20px;
	border: 1px solid transparent;
	border-radius: 0 0 3px 3px;
}

.blackhat-accordion__toggle-icon {
	top: 4px;

}

.current .blackhat-accordion__toggle-icon {
	top: 0;
	transform: rotate(-90deg);

}

