.social {
	padding: 0px;

	&__item {
		list-style-type: none;
		float: left;
		margin: 0px 4px;
	}

	a {
		display: block;
	}

	&__icon {

		svg {
			display: inline-block;
			width: 28px;
			height: 28px;
			vertical-align: middle;
			transition: $transition;

			use {
			    fill: #ffffff;
			}

			&:hover {
				opacity: 0.6;
			}
		}
	}
}