/*** Layout ***/

.layout {
	position: relative;
	width: 100%;
	padding: $grid_margin 0px;
	background-size: cover;
	background-position: center;
}

.wrapper { 
    width: 100%; 
    max-width: $outer_width; 
    margin: 0 auto; 
}

.layout--narrow .wrapper {
	width: $grid_content_narrow;
}
