.logo {
    float: left;
    margin: 0px;

    @include breakpoint($breakpoint_mobile) {
        height: auto;
    }

    &__link {
        display: block;
        font-size: 24px;
        color: #ffffff;
        vertical-align: middle;

        img {
            width: 100%;
        }

        &:hover {
            color: #eeeeee;
        }
    }
}