// Tabs
.tabs {
	background: #ffffff;
	margin-bottom: $component_margin;

	&:last-child {
		margin-bottom: 0px;
	}

	&__titles {
		@extend .clearfix;

		text-decoration: none;
		list-style-type: none;
		padding:0;

	}

	&__title  {

		float: left;
		background: $color_gray;
		border-bottom: 2px solid transparent;
		cursor: pointer;
		transition: $transition;

		&--active, &:hover {

			background: lighten($color_gray, 5%);
			border-bottom: 2px solid $color_primary;	

		}

		&[disabled] {			

			cursor: not-allowed;
			background: lighten($color_gray, 30%);

			&:hover {
				background: inherit;	
				border-bottom: 2px solid transparent;
			}
			
			.tabs__title__link {
				cursor: not-allowed;	
			}

		}

	}

	&__link {
		padding: 12px 24px;
		display: inline-block;
	}

	&__panels {
		width: 100%;
	}

	&__panel {
		display: none;
		transition: $transition;
	}

}