*
* None
*
**/
.table {
    width: 100%;
    max-width: 100%;
    margin-bottom: $component_margin;
    background-color: transparent;
    border-collapse: collapse;
    border-spacing: 0;
    overflow-x: auto;

    &__table {
        width: 100%;
    }

    &__header {
    }

    &__heading {
        vertical-align: bottom;
        border-bottom: 2px solid darken($color_gray, 10%);
        border-left: 1px solid darken($color_gray, 10%);
        background: $color_gray;
        text-align: right;
        padding: 5px 10px;

        &:first-child {
            text-align: left;
        }
    }

    &__row {
        &:hover {
            background: $color_light_gray;
        }
    }

    &__body {
        .table__row {
            &:hover {
                background: $color_light_gray;
            }
        }
    }

    &__cell {
        text-align: right;
        padding: 5px 10px;
        border-top: 1px solid $color_gray;

        &:first-child {
            text-align: left;
        }
    }

}
