.main-navigation {
    width: 100%;
    position: fixed !important;
    height: calc(100vh - 128px) !important;

    @include parent('.user-box-active') {
        height: calc(100vh - 196px) !important;
    }
}

.menu {
    list-style: none;
    padding: 0;
    margin: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent;

    &__item {

        &__icon svg {
            width: 16px;
            height: 16px;
            transform: translateY(2px);
            margin-right: 2px;
            fill: darken(#fff, 40%);
        }

        &--toggle svg {
            width: 16px;
            height: 16px;
            transform: translateY(2px);
            fill: darken(#fff, 50%);
            float: right;
        }

        &--active {

            > .menu__link {
                color: #fff;
                background: darken($color_sidebar_background, 5%);
                border-left: 5px solid $color_primary;
                font-weight: 600;

                svg {
                    fill: #fff;
                    transform: translateY(4px) rotate(-90deg);
                }
            }

            .menu--submenu {
                display: block;
            }
        }
    }

    &__link {
        position: relative;
        padding: 15px $component_margin;
        display: block;
        color: darken(#ffffff, 20%);
        border-bottom: 1px solid lighten($color_sidebar_background, 5%);
        font-weight: 300;
        will-change: background;
        transition: all .25s ease-in-out;
        transition: font-weight 0s ease-in-out;
        line-height: line-height(15, 20);
        cursor: pointer;

        &:hover {
            color: #fff;
            background: darken($color_sidebar_background, 5%);
        }
    }

    &--submenu {
        display: none;
        background: rgba(0, 0, 0, 0.5);

        .menu__item {}

        .menu__link {
            padding: 15px $component_margin*2;
            border-bottom: 1px solid lighten($color_sidebar_background, 5%);
        }
    }
}
