/**
*
* Variables
*
**/
/** Grid **/
// static/sass/layout/_blackhat_grid.scss
$grid_content: 1200px; //wrapper
$grid_content_narrow: $grid_content*0.8;
$grid_margin: 20px;
$grid_padding: 15px;

/* refractor this vars */
$grid_prefix: "col-";
$color_brand: #39b54a;
$color_button_text: #fff;
$color_button_background: $color_brand;
$color_button_text: #fff;
$color_button_background_hover: darken($color_brand,10%);
$color_button_text_hover: #fff;

$component_margin: 20px;

$header_height: 60px;
$footer_height: 80px;

/** Typography **/
// static/sass/base/_font.scss
//$font_family: Arial, Helvetica, sans-serif;
$font_family: 'Roboto', sans-serif;
$font_size: 14px;
$font_line_height: 20/14;

/** Buttons **/
$padding_button: 10px 13px;
$padding_large_button: 14px 20px;
$padding_small_button: 6px 8px;

/**
*
* Colors
*
**/

/** General **/
/** Body **/
$color_body_background: #e6e6e6;
$color_body_text: #333333;
$color_section_text: #333333;

$color_primary: #39b54a;
$color_secondary: #996AFF;
$color_featured: #CC7D2C;
$color_danger: #B2153D;

$text_color_primary: #ffffff;
$text_color_secondary: #ffffff;
$text_color_featured: #ffffff;
$text_color_danger: #ffffff;

$color_light_gray: #f7f7f7;
$color_gray: #ecedf0;
$color_dark: #333333;

/** Links **/
$color_link: #668f35;
$color_link_hover: lighten($color_link, 10);

/** Sidebar **/
$color_sidebar_background: #2a2a2a;
$sidebar_width: 250px;

/**
*
* Transitions
*
**/
$transition: all 150ms ease 0s;

/**
*
* Breakpoints
*
**/
$breakpoint_small : 440;
$breakpoint_mobile : 640;
$breakpoint_tablet : 768;
$breakpoint_desktop : 1024;
$breakpoint_laptop : 1366;
